.retreat-feature {
  min-height: 650px;
  .learnmore-button {
    padding: .9rem 1.2rem;
    h4 {
      font-size: 1rem;
    }
  }
}

.section-lg-feature-image {
  max-height: 500px;
  object-fit: cover;
  overflow: hidden;
}

.retreat-feature-content {
  h2, h3 {
    margin-bottom: 1rem;
    font-family: $header-font-family;
    font-weight: $regular_font;
    text-transform: uppercase;
  }
  p {
    margin-bottom: 2rem;
    margin-top: 2rem;
  }
}
