// Main SCSS file
@import "_resets";

$primary-color: #479AA1;
$secondary-color: #F5AEA8;
$accent-color: #D9D9D9;
$dark-color: #707070;
$light-color: #FFF;

$header-font-family: 'Raleway', sans-serif;;
$paragraph-font-family: 'Nunito', sans-serif;;

$light_font: 200;
$regular_font: 400;
$bold_font: 600;
$extra_bold_font: 700;

// font sizes
$heading1-font-size: 3rem;
$heading2-font-size: 2rem;
$heading3-font-size: 2rem;
$paragraph-font-size: 1.2rem;
$paragraph-line-height: 1.7;
$cta-button-font-size: 1rem;
$learnmore-button-font-size: .7rem;
$button-border-radius: 5px;

h1,
h2,
h3,
h4 {
  font-family: $header-font-family;
  text-transform: uppercase;
}

p {
  font-family: $paragraph-font-family;
  font-size: $paragraph-font-size;
  line-height: $paragraph-line-height;
}

@import "_utility";

@import "_navbar";
@import "_buttons";
@import "_header";
@import "_intro-section";
@import "_triple-features";
@import "_upcoming-retreat";
@import "_community-section.scss";
@import "_footer";
